import express from 'express'
import { CustomError, resFormat } from '../../../lib/middlewares/respond'
import dbconnection from '../../../lib/connection'
import { resolveCname } from 'dns';
import profile from '../index';
import { selectUserProfileList, selectPersonality, selectFeeling, selectExperience, selectUserPersonality  } from '../../../models/profile';

const getProfileService = (req: any, res: any, next: any) => {
	return new Promise(async(resolve, reject) => {
		try {
			const body = req.body;
			if (!body.question_idx){
				reject({
					code: 400,
					message: 'params에 NULL값이 존재합니다.'
				})
			}

			const connection = await dbconnection();
			const result : any = await selectUserProfileList(connection, body)
			const result1 : any = await selectPersonality(connection, body)
			const result2 : any = await selectFeeling(connection, body)
			const result3 : any = await selectExperience(connection, body)
			const result4 : any = await selectUserPersonality(connection, body)

			resolve({result, result1, result2, result3, result4})

		} catch(e){
				reject(e)
		}
	}) 
}

export default{
	getProfileService
}